.larger-header {
  margin: 0 0 30px -5rem;
  margin-left: 1.5rem;
  text-align: center;
  display: inline;
  white-space: nowrap; 
  overflow:visible; 
  text-overflow: ellipsis;
  margin-top: -5rem;
} 

label {
  display: block;
  margin-bottom: 8px;
  font-size: 1rem;
  text-align: left;
}

.type-header {
  white-space: nowrap; /* Prevent line breaks */
  overflow:visible; /* Hide overflow */
  text-overflow: ellipsis;
}




.label-text {
  margin-left: 15px;
}

.option2 {
  margin-bottom: 20px;
}


