input[type="name"],
input[type="password"],
input[type="email"],
input[type="doctorsearch"]
select {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 10px;
  font-size: 16px;
  height: auto;
  outline: 0;
  padding: .7rem;
  width: 100%; /* Set the width to 100% to fill the container */
  background-color: #e8eeef;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
  margin-bottom: 30px;
  text-align: left;
}

button {
  border: none;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  cursor: pointer;
  color: #000000;
  font-size: 1rem;
  transition: background-color 0.3s ease;
  border-radius: 25px;
  font-weight: bold;
  margin-left: 1.7rem;
}

button:hover {
  background-color: rgb(166, 208, 239);
}

